<template>
    <div>
        <router-view v-if="$store.state.chronus"/>
        <div v-else>
            <chronus v-if="$store.state.token"></chronus>
            <chronus-now v-else></chronus-now>
        </div>
    </div>
</template>

<script>
import ChronusNow from './components/Conning/ChronusNow.vue'
import Init from './components/Data/Init.vue'
import Chronus from './pages/Home/Chronus/Chronus.vue'

export default {
    components: { 
      Init,  
      Chronus,
      ChronusNow 
    },
    name: 'App',
    props: {
        _chronu: {
            type: Object
        },
    },
    data() {
        return {
            update_chronu: false
        }
    },
    mounted() {
        // APP 检查
        this.appCheck()
    },
    methods: {
        async appCheck() {
            const needAuth = this.$store.getters.checkToken()
            const needLogin = this.$store.getters.checkUserBackend()
            
            if (!needLogin == true && !needAuth == true) {
                this.update_chronu = this.$store.getters.checkChronus()
            } else {
                this.$store.commit('changeAuth', '')
                this.$store.commit('saveBackendUser', null)
                // this.$router.push({ path: '/login' })
                this.update_chronu = true
            }
            if (this.update_chronu) { 
                let res = await this.conn.get(
                    this.api.chronus,
                    this.$store.state.token,
                    {
                    '_where[start_lte]': this.view.getToday(),
                    '_where[end_gte]': this.view.getToday()
                    }
                )
                if (res) {
                    if (res.length > 0) { res = res[0]; this.$store.commit('saveChronus', [ res, true ]) }
                }
                
            }

            console.log('NEED_AUTH =', needAuth, ',NEED_LOGIN =', needLogin, ',INTO INIT =', (!needLogin == true && !needAuth == true))
        
        },
    }
}
</script>

<style lang="sass">

</style>



