<template>
  <v-app id="app">
    <data-from-wordpress @recive_Father="reciveWordpressData"></data-from-wordpress>

    <air-init :_chronu="chronu"></air-init>
  </v-app>
</template>

<script>
import AirInit from './AirInit.vue'
import DataFromWordpress from './components/Conning/WordPress/DataFromWordpress.vue'

export default {
  components: { 
    AirInit,
    DataFromWordpress 
  },
  name: 'App',
  data() {
    return {
      chronu: null,
      plant_data: null
    }
  },
  mounted() {
    // 当前月份
    this.chronusNow()
  },
  watch: {
    plant_data(n, older) {
      // 平台数据不为空，则启用 储存设施，保存数据为后面所用
      if (n) {
        console.log('监听成功！！！')
        this._plantform_Check()
      }
    }
  },
  methods: {
    async chronusNow() {
      let res = await this.conn.get(
        this.api.chronus,
        this.$store.state.token,
        {
          '_where[start_lte]': this.view.getToday(),
          '_where[end_gte]': this.view.getToday()
        }
      )
      if (res) {
        if (res.length > 0) { this.chronu = res[0] }
      } else {
        this.chronu = null
        this.view.alert_message('新的結算日程開始了，但是數據庫未有關於新日程的時間準備，請移步後臺新增時間代號。')
      }
    },

    _plantform_Check() {
      let _update = true
      
      if (!this.$store.getters.checkPlantfrom()) {
        if (this.$store.state.platform_data.userID == this.plant_data.userID) {
          _update = false
        }
      }

      if (_update) {
        console.log('来自平台的 ID =', this.plant_data.userID)
        this.$store.commit('saveFromPlatform', this.plant_data)
      } else {
        console.log('来自平台的 ID =', this.$store.state.platform_data.userID)
      }
    },

    reciveWordpressData(data) {
      try {
        this.plant_data = data.params
      } catch(e) {

      }
    }
  },
}

// SEND MSG TO PARENT
window.parent.postMessage({
  cmd: 'returnHeight',
  params: {
    success: true,
    data: document.body.scrollHeight + 'px'
  }
}, '*');
</script>

<style lang="sass">
@import "@/common/sass/base.sass"
</style>



